@charset "UTF-8";
html {
  height: 100%;
}

body {
  min-height: 100%;
  margin: 0 auto;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #444444;
}

img {
  max-width: 100%;
  object-fit: contain;
}

@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 400;
  src: url("./../fonts/opensans.woff2") format("woff2"), url("./../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  src: url("./../fonts/opensansbold.woff2") format("woff2"), url("./../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.main {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main {
    position: relative;
    z-index: 2;
    top: -46px;
  }
}

.main__title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 16px 0;
  background-color: #ececec;
  font-size: 27px;
  line-height: 30px;
  min-height: 177px;
  color: #000000;
}
@media (min-width: 768px) {
  .main__title {
    min-height: 240px;
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 53px;
    padding-top: 14px;
    box-sizing: border-box;
  }
}
@media (min-width: 1150px) {
  .main__title {
    min-height: 272px;
    font-size: 53px;
    line-height: 57px;
    margin-bottom: 98px;
    padding: 0 0 11px 0;
  }
}
@media (min-width: 768px) {
  .main__title::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: url("./../img/zigzag-fill-desktop.svg") repeat-x;
    background-size: contain;
    height: 24px;
    z-index: 3;
  }
}
@media (min-width: 1150px) {
  .main__title::before {
    height: 34px;
  }
}

.main__main-header {
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .main__main-header {
    margin-bottom: 54px;
  }
}
@media (min-width: 1150px) {
  .main__main-header {
    margin-bottom: 71px;
  }
}

.main__featured {
  margin-bottom: 73px;
}
@media (min-width: 768px) {
  .main__featured {
    margin-bottom: 125px;
  }
}
@media (min-width: 1150px) {
  .main__featured {
    margin-bottom: 164px;
  }
}

.main__about {
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .main__about {
    margin-bottom: 63px;
  }
}
@media (min-width: 1150px) {
  .main__about {
    margin-bottom: 88px;
  }
}

.main__review {
  margin-bottom: 89px;
}
@media (min-width: 768px) {
  .main__review {
    margin-bottom: 116px;
  }
}
@media (min-width: 1150px) {
  .main__review {
    margin-bottom: 161px;
  }
}

.main__contacts {
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .main__contacts {
    margin-bottom: 11px;
  }
}
@media (min-width: 1150px) {
  .main__contacts {
    margin-bottom: 59px;
  }
}

.main__catalog {
  margin-bottom: 30px;
  margin-top: 15px;
}
@media (min-width: 768px) {
  .main__catalog {
    margin-bottom: 53px;
    margin-top: 12px;
  }
}
@media (min-width: 1150px) {
  .main__catalog {
    margin-bottom: 57px;
  }
}

.main__video {
  margin: 0 30px 58px 30px;
}
@media (min-width: 768px) {
  .main__video {
    margin: 0 50px 18px 50px;
  }
}
@media (min-width: 1150px) {
  .main__video {
    margin: 0 auto 59px auto;
    min-width: 1050px;
  }
}

.main__order {
  margin-bottom: 55px;
}
@media (min-width: 768px) {
  .main__order {
    margin-bottom: 5px;
  }
}
@media (min-width: 1150px) {
  .main__order {
    margin-bottom: 3px;
  }
}

.button {
  width: 100%;
  min-height: 52px;
  border: 2px solid #444444;
  color: #444444;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 17px;
  line-height: 24px;
  text-transform: uppercase;
  font-weight: 700;
  background-color: #ffffff;
  outline: none;
  margin: 0;
  padding: 0;
}
.button:focus {
  border-color: #63d1bb;
  color: #63d1bb;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
  border-color: #444444;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.7);
}

/* Класс для скрытия элементов */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  position: relative;
  min-height: 80px;
  background-color: #ffffff;
}
.page-header::before {
  content: "";
  position: absolute;
  top: 79px;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #ececec;
}
@media (min-width: 768px) {
  .page-header::before {
    bottom: 46px;
    top: auto;
  }
}

.page-header__wrapper {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 10;
  display: grid;
  grid-template-columns: 1fr;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    position: relative;
    padding: 0 50px;
    grid-template-columns: 1fr 28px 1fr;
    grid-template-rows: 1fr 46px;
    grid-auto-flow: column;
    grid-template-areas: "header-logo user-nav user-nav" "site-nav site-nav delivery-info";
  }
}
@media (min-width: 1150px) {
  .page-header__wrapper {
    max-width: 1050px;
    padding: 0;
    margin: 0 auto;
    grid-template-columns: 1fr 150px 1fr;
    grid-template-rows: 1fr 46px;
    grid-template-areas: "site-nav header-logo user-nav" "sale-info . delivery-info";
  }
}
.page-header__wrapper--nojs {
  position: relative;
}

.page-header__toggle {
  position: absolute;
  right: 21px;
  top: 39px;
  transform: translateY(-50%);
  border: none;
  width: 40px;
  height: 40px;
}
.page-header__toggle--open {
  background: url("./../img/header-menu-open.svg") #ffffff no-repeat 50% 50%;
}
.page-header__toggle--closed {
  background: url("./../img/header-menu-closed.svg") #ffffff no-repeat 50% 50%;
}
.page-header__toggle--nojs {
  display: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__nav {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .page-header__nav--closed .site-nav__list {
    display: none;
  }
  .page-header__nav--closed .user-nav__list {
    display: none;
  }
}
@media (width: 767px) {
  .page-header__nav--closed .site-nav__list {
    display: none;
  }
  .page-header__nav--closed .user-nav__list {
    display: none;
  }
}
.page-header__nav--open.user-nav {
  position: relative;
  height: auto;
  z-index: 10;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header__nav--open.user-nav {
    background-color: inherit;
  }
}
.page-header__nav--open.site-nav {
  position: relative;
  height: auto;
  z-index: 10;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header__nav--open.site-nav {
    background-color: inherit;
  }
}
.page-header__nav--nojs {
  position: relative;
  display: block;
}

.page-header__logo {
  min-height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
}
@media (min-width: 768px) {
  .page-header__logo {
    min-height: 88px;
    grid-area: header-logo;
    align-self: center;
    justify-self: start;
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    justify-self: center;
  }
}
.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .page-header__image {
    display: block;
    width: 113px;
    height: auto;
  }
}
@media (min-width: 1150px) {
  .page-header__image {
    width: 149px;
  }
}

.page-header__picture {
  display: flex;
  align-items: center;
}

.page-header__delivery-info {
  display: none;
}
@media (min-width: 768px) {
  .page-header__delivery-info {
    display: block;
    grid-area: delivery-info;
    align-self: flex-end;
    justify-self: end;
    z-index: 3;
    line-height: 26px;
  }
}

.page-header__sale-list {
  display: none;
}
@media (min-width: 1150px) {
  .page-header__sale-list {
    z-index: 3;
    grid-area: sale-info;
    display: flex;
    align-self: flex-end;
    justify-self: start;
    list-style-type: none;
    padding: 0;
    margin: 0;
    gap: 62px;
  }
}

@media (min-width: 1150px) {
  .page-header__sale-link {
    font-size: 15px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
    text-decoration: none;
    outline: none;
  }
}
.page-header__sale-link:focus {
  color: #63d1bb;
}
.page-header__sale-link:hover {
  opacity: 0.6;
}
.page-header__sale-link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .site-nav {
    grid-area: site-nav;
    align-self: center;
    justify-self: flex-start;
  }
}

.site-nav__list {
