.featured {
  position: relative;
}

.featured__wrapper {
  position: relative;
  padding: 0 30px;
  display: grid;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .featured__wrapper {
    padding: 0 50px;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-areas: "f-title f-title f-subtitle" "f-text f-image f-image" "f-description f-image f-image" "f-price f-image f-image" "f-button f-image f-image";
  }
}
@media (min-width: 1150px) {
  .featured__wrapper {
    margin: 0 auto;
    max-width: 1050px;
    padding: 0;
    grid-template-columns: 1fr 2fr 1fr 2fr;
    grid-template-rows: max-content max-content max-content 1fr max-content;
    row-gap: 33px;
    grid-template-areas: "f-subtitle f-title f-image f-image" ". f-text f-image f-image" ". f-description f-image f-image" ". f-price f-image f-image" ". f-button f-image f-image";
  }
}
.featured__wrapper::after {
  content: "";
  position: absolute;
  height: 6px;
  background: url("./../img/zigzag-line-desktop.svg") repeat-x;
  bottom: -42px;
  left: 0;
  right: 0;
}
@media (min-width: 768px) {
  .featured__wrapper::after {
    bottom: -68px;
    height: 17px;
    background: url("./../img/zigzag-line-desktop2.svg") repeat-x -21px;
    background-size: contain;
  }
}
@media (min-width: 1150px) {
  .featured__wrapper::after {
    height: 23px;
    bottom: -96px;
  }
}

.featured__title {
  margin: 0 0 4px 0;
  padding: 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
